.loading{
  width: 74px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform:translate(-50%,-50%);
  z-index: 100;
}

.arrowStroke{
  fill:none;
  stroke:#000;
  stroke-linecap:round;
  stroke-miterlimit:10;
}
.heartStroke1{
  fill:rgba(0,0,0,1);
  stroke:#000;
  stroke-width:.5;
  stroke-linecap:round;
  stroke-miterlimit:10;

  animation: heartAni 1s linear infinite;
}

@keyframes heartAni {

  0% {
    fill:rgba(0,0,0,0);
  }

  24% {
    fill:rgba(0,0,0,0);
  }

  25% {
    fill:rgba(0,0,0,1);
  }

  100% {
    fill:rgba(0,0,0,1);
  }
}
.heartStroke2{
  fill:rgba(0,0,0,0);
  stroke:#000;
  stroke-width:.5;
  stroke-linecap:round;
  stroke-miterlimit:10;

  animation: heartAni2 1s linear infinite;
}

@keyframes heartAni2 {

  0% {
    fill:rgba(0,0,0,0);
  }

  49% {
    fill:rgba(0,0,0,0);
  }

  50% {
    fill:rgba(0,0,0,1);
  }

  100% {
    fill:rgba(0,0,0,1);
  }
}

.heartStroke3{
  fill:rgba(0,0,0,0);
  stroke:#000;
  stroke-width:.5;
  stroke-linecap:round;
  stroke-miterlimit:10;

  animation: heartAni3 1s linear infinite;
}

@keyframes heartAni3 {

  0% {
    fill:rgba(0,0,0,0);
  }

  74% {
    fill:rgba(0,0,0,0);
  }

  75% {
    fill:rgba(0,0,0,1);
  }

  100% {
    fill:rgba(0,0,0,1);
  }
}

.loadingStroke{
  fill:none;
  stroke:#000;
  stroke-linecap:round;
  stroke-miterlimit:10;
  stroke-dasharray: 1600;
  stroke-dashoffset: 1600;

  animation: loadingAni 2s ease-in-out infinite;
  // animation-delay:1s;
}

@keyframes loadingAni {

  0% {
    stroke-dashoffset: 1600;
  }

  /* Finish changes by here */
  50% {
    stroke-dashoffset: 0;
  }

  /* Between 20% and 100%, nothing changes */
  100% {
    stroke-dashoffset: 0;
  }
}

